/* add here your styles */
// prettier-ignore
.staff-selector {

    .Select--multi {

        .Select-control {
            height: 40px;
        }

        .Select-value {
            position: relative;
            display: inline-block;
            min-width: 10px;
            margin-left: 5px;
            padding: 3px 7px;
            color: #ffffff;
            background-color: #777777;
            border-radius: 10px;
            font-size: 12px;
            font-weight: 700;
            line-height: 1;
            text-align: center;
            vertical-align: middle;
            white-space: nowrap;
        }

        .Select-value-icon {
            padding: 0;
            cursor: pointer;
        }

    }
}

.badge {
    padding: 5px 9px;
}

ul.pointer {
    display: inline-block;
}

.expand-all {
    margin-right: 15px;
    margin-bottom: 10px;
}

.default-assignment-container {
    padding-right: 15px;
    padding-left: 15px;
}



